
*{
  font-family: Helvetica,Arial, sans-serif;
}

body{
  padding: 15px;
}

input{
  color:grey;
  font-size: 1rem;
  width: 80%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid red;
  border-radius: 6px;
  overflow:hidden;
  margin-bottom: 10px;
  margin-right: 10px
}
.card p {
    padding: 5px;
    text-align: center;
}

.card img{

  max-height: 170px;
}